import { configure, observable, runInAction, action } from "mobx";
import { toast } from "react-toastify";
import investmentProvider from "../services/investment.provider";

// Configuration
configure({
    enforceActions: "observed",
});

class InvestmentStore {
    @observable
    trailAccounts = [];

    @observable
    investments = [];

    // loading indicator
    @observable
    loading = false;

    // message
    @observable
    errorMessage = "";

    @observable
    props = {
        page: 1,
        total: 0,
        pages: 0,
        limit: 24,
    };

    @action("Trial Investment Accounts")
    GetTrailAccount = () => {
        // reset
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        const { page, limit } = this.props;
        investmentProvider
            .getTrialAccounts(page, limit)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.props.limit = cb.limit;
                        this.props.pages = cb.pages;
                        this.props.page = cb.page;
                        this.props.total = cb.total;
                        this.trailAccounts = cb.docs;
                    } else {
                        toast(cb.message, {
                            position: toast.POSITION.TOP_CENTER,
                            autoClose: 8000,
                            type: toast.TYPE.ERROR,
                        });
                        this.errorMessage = cb.message;
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check your internet connection and try again.";
                    toast(this.errorMessage, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };

    @action
    LoadMore = () => {
        if (this.props.pages > this.props.page) {
            runInAction(() => {
                this.props.page++;
            });
            this.GetTrailAccount();
        } else {
            toast("No more record to fetch!", {
                position: toast.POSITION.TOP_CENTER,
                type: toast.TYPE.ERROR,
            });
        }
    };

    @action
    SearchUserInvestment = (keyword) => {
        // reset
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
            this.investments = [];
        });
        investmentProvider
            .SearchInvestment(keyword)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.investments = cb.docs;
                    } else {
                        toast(cb.message, {
                            position: toast.POSITION.TOP_CENTER,
                            autoClose: 8000,
                            type: toast.TYPE.ERROR,
                        });
                        this.errorMessage = cb.message;
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check your internet connection and try again.";
                    toast(this.errorMessage, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };

    @action
    UpdateBalance = (id, newValue, index) => {
        // reset
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        investmentProvider
            .updateBalance(id, newValue)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.investments[index].currentBalance = newValue;
                    } else {
                        toast(cb.message, {
                            position: toast.POSITION.TOP_CENTER,
                            autoClose: 8000,
                            type: toast.TYPE.ERROR,
                        });
                        this.errorMessage = cb.message;
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check your internet connection and try again.";
                    toast(this.errorMessage, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };
    @action
    UpdateInvestmentMade = (id, newValue, index) => {
        // reset
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        investmentProvider
            .updateInvestmentMade(id, newValue)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.investments[index].InvestmentMade = newValue;
                    } else {
                        toast(cb.message, {
                            position: toast.POSITION.TOP_CENTER,
                            autoClose: 8000,
                            type: toast.TYPE.ERROR,
                        });
                        this.errorMessage = cb.message;
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check your internet connection and try again.";
                    toast(this.errorMessage, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };

    @action("Reinvest")
    ReInvest = (id, email) => {
        // reset
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        investmentProvider
            .ReInvestmen(id)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        document.location.reload();
                    } else {
                        toast(cb.message, {
                            position: toast.POSITION.TOP_CENTER,
                            autoClose: 8000,
                            type: toast.TYPE.ERROR,
                        });
                        this.errorMessage = cb.message;
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check your internet connection and try again.";
                    toast(this.errorMessage, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };
}

export default new InvestmentStore();
