import React, { Component } from "react";
import { Helmet } from "react-helmet";
import ActivePage from "../../partials/activepage";
import { inject, observer } from "mobx-react";
import LoadingIndicator from "../../partials/loading/loading";
import InvestmentStatictis from "./investment.statistics";
import authProvider from "../../../services/auth.provider";
import NoContent from "../../partials/NoContent";
import SingleInvestment from "./investment.single";
import * as QRCode from "qrcode.react";
import { utility } from "../../../utility/core";
import $ from "jquery";
import { CSSTransition } from "react-transition-group";

@inject("transactionStore", "planStore", "investmentStore")
@observer
class UserInvestment extends Component {
    state = {
        newInvestment: this.props.transactionStore.newInvestmentObject,
        currencyId: "",
        amount: 0,
        name: "",
        address: "",
        id: "",
        currency: "",
        ids: [],
        merge: {
            service: "",
            fundTarget: "5e18c7065799a61510840f94",
            amount: 0,
        },
    };
    // will mount
    componentDidMount() {
        const { planStore, transactionStore } = this.props;
        const { id } = authProvider.getUser();
        if (planStore.isEmpty) {
            planStore.getPlans();
        }

        transactionStore.getInvestment(id);
        if (!planStore.hasCurrency) planStore.getCurrencies();
    }
    // render content
    renderContent() {
        const { transactionStore } = this.props;
        if (transactionStore.investments.length) {
            return (
                <div className="row">
                    <div className="col-12">
                        {transactionStore.investments.length > 1 && (
                            <div className="text-right mb-4">
                                <button
                                    onClick={() => {
                                        if (this.state.ids.length > 1) {
                                            document.getElementById("merge").click();
                                        } else alert("You must select more than 1 investment to be able to merge!");
                                    }}
                                    className="btn btn-primary"
                                >
                                    Merge Investment
                                </button>
                            </div>
                        )}
                    </div>
                    {transactionStore.investments.map((doc) => (
                        <CSSTransition in={true} key={doc.id} appear={true} timeout={1000} classNames="slide">
                            <SingleInvestment
                                onReinvest={this.handleReinvest}
                                onSelect={this.onSelect}
                                onWithdraw={this.onWithdraw}
                                onTopup={this.onTopup}
                                onClose={this.onclose}
                                key={doc.id}
                                model={doc}
                            />
                        </CSSTransition>
                    ))}
                </div>
            );
        } else {
            return <NoContent message={transactionStore.investmentFetchOption.message} />;
        }
    }

    onSelect = (id, amount) => {
        const items = [...this.state.ids];
        let _amount = this.state.merge.amount;
        const idx = items.indexOf(id);
        if (idx !== -1) {
            items.splice(idx, 1);
            _amount -= amount;
        } else {
            items.push(id);
            _amount += amount;
        }
        this.setState({ ids: items, merge: { ...this.state.merge, amount: _amount } });
    };

    // handle  withdraw
    handleWithdraw = () => {
        const { id, amount, currency } = this.state;
        this.props.transactionStore.sendWithdrawRequest(id, amount, currency);
    };
    // accept amount
    onWithdraw = (id, amount, currency) => {
        this.setState({ id: id });
        this.setState({ amount: amount });
        this.setState({ currency: currency });
        $("#w_t").click();
    };

    // accept amount
    onTopup = (id, currency, investmentId) => {
        this.setState({ id: id });
        this.setState({ currency: currency });
        this.setState({ investmentId: investmentId });
        $("#openTopup").click();
    };
    onclose = (id) => {
        this.setState({ id: id });
        $("#closeBox").click();
    };

    // handle submit
    handleCreate = (amount, currencyId, fundtype, cardModel) => {
        // get plan
        const plan = this.props.planStore.getPlanByAmount(amount);
        this.props.transactionStore.newInvestment(amount, currencyId, authProvider.getUser().id, fundtype, plan ? plan.percent : 0, cardModel).then((cb) => {
            if (cb.status === 200) {
                this.setState({ amount: amount });
                this.setState({ currencyId: currencyId });
                if (currencyId) {
                    const { name, address } = this.props.planStore.getCurrency(currencyId);
                    this.setState({ name: name });
                    this.setState({ address: address });
                }

                this.setState({ id: cb.doc.id });

                $("#trg").click();
            }
        });
    };

    handleMerge = (event) => {
        event.preventDefault();
        document.getElementById("merge").click();
        const plan = this.props.planStore.getPlanByAmount(this.state.merge.amount);
        const { ids, merge } = this.state;
        this.props.transactionStore.Merge(authProvider.getUser().id, plan ? plan.percent : 0, merge.fundTarget, ids, merge.service).then((cb) => {
            if (cb.status === 200) {
                document.location.reload();
            }
        });
    };

    // handle review
    handleClose = () => {
        this.props.transactionStore.closeUserSingleInvestment(this.state.id);
    };
    // handle review
    handleReinvest = (id) => {
        this.props.investmentStore.ReInvest(id, authProvider.getUser().email);
    };

    // handle review
    handleReview = () => {
        this.props.transactionStore.reviewTransaction(this.state.id);
    };
    // handle top up
    handletopupup = () => {
        this.props.transactionStore.topupInvestmentMade(this.state.amount, this.state.currency, this.state.investmentId);
        this.setState({ amount: 0 });
    };
    // copy text
    copyToClipboard = () => {
        utility.copyToClipboard("cont", this.state.address);
    };

    //onChange
    onChange = (ev) => {
        this.setState({ [ev.target.name]: ev.target.value });
    };

    render() {
        const title = "Your Investment";
        return (
            <React.Fragment>
                <Helmet>
                    <title>
                        {title} | {utility.GetAppName()}
                    </title>
                </Helmet>
                <ActivePage title={title} />
                <CSSTransition in={true} appear={true} timeout={1000} classNames="slide">
                    <InvestmentStatictis currencies={this.props.planStore.currency.currencies} onCreate={this.handleCreate} models={this.props.transactionStore.investments} />
                </CSSTransition>

                {(this.props.planStore.loading || this.props.transactionStore.loading) && <LoadingIndicator useBackdrop={true} />}

                {!this.props.planStore.loading && this.renderContent()}

                <input id="trg" data-target="#ff2" data-toggle="modal" type="button" hidden="hidden" />
                <div className="modal fade" id="ff2" tabIndex="-1" role="dialog" aria-labelledby="xp_2" aria-hidden="true">
                    <div className="modal-dialog" role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="xp_2">
                                    Pay for Investment Fund!
                                </h5>
                                <button className="close" type="button" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true"></span>
                                </button>
                            </div>
                            <div className="modal-body">
                                <div className="text center m-4">
                                    {this.state.currencyId && <p>New Investment has been added. Make a transfer of</p>}

                                    <div className="row">
                                        {this.state.currencyId && (
                                            <React.Fragment>
                                                <div className="col">
                                                    <p>
                                                        <b>Amount</b>:{" "}
                                                        {Intl.NumberFormat("en-US", {
                                                            currency: "USD",
                                                            style: "currency",
                                                        }).format(this.state.amount)}
                                                    </p>
                                                    <p>
                                                        <b>Currency</b> : {this.state.name || "coin name"}
                                                    </p>
                                                    <p>
                                                        <b>Wallet Address</b>
                                                    </p>
                                                </div>
                                                <div className="col">
                                                    <QRCode value={this.state.address} fgColor="#1d2124" />
                                                </div>
                                            </React.Fragment>
                                        )}
                                    </div>
                                    {this.state.currencyId && (
                                        <div id="cont" className="copy-button mb-1">
                                            <input type="text" value={this.state.address} placeholder={this.state.address} readOnly={true} className="form-control copy_input" name="address" />
                                            <span className="copy-link btn-dark" onClick={this.copyToClipboard} data-toggle="tooltip" title="Copy to Clipboard">
                                                Copy
                                            </span>
                                        </div>
                                    )}
                                    {this.state.currencyId && <p>After your transfer is successfully completed, click on the button below to continue.</p>}
                                    {!this.state.currencyId && (
                                        <div className="text-center">
                                            <h5 className="text-muted">Payment is being processed!</h5>
                                            <p>click on the button below to continue.</p>
                                        </div>
                                    )}
                                </div>
                                <div className="text-right">
                                    <button className="btn btn-default mb-4" type="button" onClick={this.handleReview} data-dismiss="modal">
                                        Continue
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* new fund withdrawal */}
                <input type="hidden" data-target="#withdrawForm" data-toggle="modal" id="w_t" />
                <input type="hidden" data-target="#cancelBox" data-toggle="modal" id="closeBox" />
                <input type="hidden" data-target="#topupForm" data-toggle="modal" id="openTopup" />

                <div className="modal fade" id="topupForm" tabIndex="-1" role="dialog" data-backdrop="static " data-keyboard="false" aria-labelledby="zwf_44" aria-hidden="true">
                    <div className="modal-dialog " role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="zwf_44">
                                    Investment Topup Request
                                </h5>
                            </div>
                            <div className="modal-body">
                                <div className="row">
                                    <div className="form-group col-md-12">
                                        <label className="col-form-label">Amount(USD)</label>
                                        <div className="input-group">
                                            <input
                                                type="number"
                                                name="amount"
                                                min="0"
                                                value={this.state.amount}
                                                placeholder="e.g 5000.00"
                                                autoFocus={true}
                                                onChange={this.onChange}
                                                className="form-control"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button data-dismiss="modal" className="btn btn-light">
                                    Cancel
                                </button>
                                <button className="btn btn-default" type="button" onClick={this.handletopupup} data-dismiss="modal">
                                    Send Request
                                    <i className="icon-arrow-right icons" />
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="modal fade" id="withdrawForm" tabIndex="-1" role="dialog" data-backdrop="static " data-keyboard="false" aria-labelledby="zwf_4" aria-hidden="true">
                    <div className="modal-dialog " role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="zwf_4">
                                    Withdraw Request
                                </h5>
                            </div>
                            <div className="modal-body">
                                <div className="row">
                                    <div className="form-group col-md-12">
                                        <label className="col-form-label">Amount(USD)</label>
                                        <div className="input-group">
                                            <input
                                                type="number"
                                                name="amount"
                                                min="0"
                                                value={this.state.amount}
                                                placeholder="e.g 5000.00"
                                                autoFocus={true}
                                                onChange={this.onChange}
                                                className="form-control"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button data-dismiss="modal" className="btn btn-light">
                                    Cancel
                                </button>
                                <button className="btn btn-default" type="button" onClick={this.handleWithdraw} data-dismiss="modal">
                                    Send Request
                                    <i className="icon-arrow-right icons" />
                                </button>
                            </div>
                        </div>
                    </div>
                </div>

                {/* close dialog box */}
                <div className="modal fade" id="cancelBox" tabIndex="-1" role="dialog" aria-labelledby="35_45f" aria-hidden="true">
                    <div className="modal-dialog" role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="35_45f">
                                    Close investment ?
                                </h5>
                            </div>
                            <div className="modal-body">Are you sure you want to close this investment? This can not be undone.</div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-info" data-dismiss="modal">
                                    Cancel
                                </button>
                                <button onClick={this.handleClose} type="button" data-dismiss="modal" className="btn btn-light">
                                    Yes <i className="icon-arrow-right icons" />
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
                {/* MERGE */}
                <input type="hidden" data-target="#merge_model" data-toggle="modal" id="merge" />
                <div className="modal fade" id="merge_model" tabIndex="-1" role="dialog" data-backdrop="static" data-keyboard="false" aria-labelledby="nv_879" aria-hidden="true">
                    <div className="modal-dialog" role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="nv_879">
                                    Merge Investment
                                </h5>
                                <button className="close" type="button" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true"></span>
                                </button>
                            </div>
                            <div className="modal-body">
                                <form onSubmit={this.handleMerge}>
                                    <div className="form-group">
                                        <label className="form-label">Select Service</label>
                                        <div className="input-group">
                                            <div className="select_box">
                                                <select
                                                    name="service"
                                                    required
                                                    value={this.state.merge.service}
                                                    onChange={({ currentTarget: { value } }) => {
                                                        this.setState({ merge: { ...this.state.merge, service: value } });
                                                    }}
                                                    className="form-control"
                                                >
                                                    <option value=""> Select Service</option>
                                                    {this.props.planStore.services.list.map((c) => (
                                                        <option key={c.id} value={c.name}>
                                                            {c.name}
                                                        </option>
                                                    ))}
                                                </select>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="text-right">
                                        <button className="btn btn-default" type="submit">
                                            Merge <i className="icon-cloud-upload icons" />
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default UserInvestment;
