import React, { Component, Fragment } from "react";
import { inject, observer } from "mobx-react";
/**
 * Render user single investment
 * @requires plan name of plan of such investment
 * @requires expectedProfit a computed value from investment made and percentage user's plan
 * @requires model json object containing user's investment made and current balance
 *
 */
@inject("planStore")
@observer
class SingleInvestment extends Component {
    // state
    state = {
        plan: "",
        expectedProfit: 0,
        doc: this.props.model,
        amount: 0,
        id: "",
        currency: "",
        percent: 100,
    };

    // get class
    optionButton(amount, id, cb, currency) {
        if (amount) {
            return (
                <Fragment>
                    <button type="button" onClick={() => this.withdrawReques(id, cb, currency)} className="btn btn-default">
                        <i className="icon-paypal icons" /> Withdraw
                    </button>
                    <button type="button" onClick={() => this.props.onReinvest(id)} className="btn btn-secondary ml-2">
                        <i className="icon-refresh  icons" /> Re-invest
                    </button>
                </Fragment>
            );
        } else {
            return (
                <button onClick={() => this.closeInvestment(id)} className="btn btn-light text-danger">
                    <i className="fa fa-stop-circle" /> Close
                </button>
            );
        }
    }

    // withdraw Request
    withdrawReques = (id, amount, currency) => {
        this.props.onWithdraw(id, amount, currency);
    };
    // withdraw Request
    closeInvestment = (id) => {
        this.props.onClose(id);
    };

    //
    topup = (id, currency, investmentId) => {
        this.props.onTopup(id, currency, investmentId);
    };

    getPayable = (amount, _percent) => {
        const percent = _percent / this.state.percent;
        // result
        return Math.floor(percent * amount);
    };
    getTitle = (amount) => {
        const plan = this.props.planStore.getPlanByAmount(amount);
        return plan ? plan.title : "";
    };
    render() {
        const { model } = this.props;
        return (
            <div className="col-xl-4">
                <div className="card mb-4">
                    <div className="card-sm-header">
                        <span className="wallet-heading">
                            <i className={`cc ${(model.currency && model.currency.icon) || "NOTE"}`} title={model.currency && model.currency.icon} />
                            <h4 title="Investment Plan"> {this.getTitle(model.investmentMade) || "Plan Name"}</h4>
                        </span>
                    </div>
                    <div className="card-body">
                        <ul className="list_none p-0 wallet_info">
                            <li>
                                <span>Investment Made</span>
                                <span>
                                    {Intl.NumberFormat("en-US", {
                                        currency: "USD",
                                        style: "currency",
                                    }).format(model.investmentMade)}
                                </span>
                            </li>
                            <li>
                                <span>Expected Profit</span>
                                <span>
                                    <strong>
                                        {Intl.NumberFormat("en-US", {
                                            currency: "USD",
                                            style: "currency",
                                        }).format(this.getPayable(model.investmentMade, model.percent))}
                                    </strong>
                                </span>
                            </li>
                            <li>
                                <span>Current Balance</span>
                                <span>
                                    {Intl.NumberFormat("en-US", {
                                        currency: "USD",
                                        style: "currency",
                                    }).format(model.currentBalance)}
                                </span>
                            </li>
                            <li>
                                <span>Selected Service</span>
                                <span>{model.targetService}</span>
                            </li>
                            <li>
                                <span>Payment Source</span>
                                <span>{(model.currency && model.currency.name) || "Credit Card"}</span>
                            </li>
                        </ul>
                        <div className="text-center">{this.optionButton(model.currentBalance, model.id, model.currentBalance, (model.currency && model.currency.name) || "Credit Card")}</div>
                    </div>

                    <div className="card footer">
                        <div className="custom-control custom-checkbox">
                            <input onChange={() => this.props.onSelect(model.id, model.investmentMade)} type="checkbox" class="custom-control-input" id={model.id} />
                            <label className="custom-control-label" htmlFor={model.id}>
                                Select
                            </label>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default SingleInvestment;
