import { action, computed, observable, configure, runInAction } from "mobx";
import investmentService from "../services/investment.provider";
import transactionService from "../services/transaction.provider";
import { toast } from "react-toastify";
import { endpoint } from "../model/events";
import { utility } from "../utility/core";
import openSocekt from "socket.io-client";
import { pushNotificationProvider } from "../services/PushNotificationService";
import authProvider from "../services/auth.provider";

// allow model modification only from an action
configure({
    enforceActions: "observed",
});

class TransactionStore {
    constructor() {
        //
        this.push = pushNotificationProvider;
        this.socket = openSocekt(utility.getUrl() + "/request", () => {});
        this.push.requestPermission();
        this.listenToUpdate();
    }

    listenToUpdate() {
        // on new fund requet
        this.socket.on(endpoint.new_fund_request, (data) => {
            runInAction(() => {
                this.investments.unshift(data.item);
            });
            const { admin } = (authProvider.isAuthenticated() && authProvider.getUser()) || false;
            if (admin)
                this.push.generateNotification(
                    "New Fund Request",
                    `New fund request has been added: Amount (USD) : ${Intl.NumberFormat("en-US", {
                        style: "currency",
                        currency: "USD",
                    }).format(data.amount)} | Currency: ${data.currency.name}  `
                );
        });

        // on new witndraw request
        this.socket.on(endpoint.new_withdraw_request, (data) => {
            runInAction(() => {
                this.withdrawRequests.unshift(data.item);
            });
            const { admin } = (authProvider.isAuthenticated() && authProvider.getUser()) || false;
            if (admin)
                this.push.generateNotification(
                    "New Withdral Request!",
                    `New Withdrawal request has been sent: Amount(USD):${Intl.NumberFormat("en-US", {
                        style: "currency",
                        currency: "USD",
                    }).format(data.amount)} | Currency: ${data.currency || data.currency.name} `
                );
        });

        // On Approval
        this.socket.on(endpoint.approve_transaction, (item) => {
            const { id, email } = authProvider.isAuthenticated() && authProvider.getUser();
            runInAction(() => {
                if (item.to === id) this.push.generateNotification("Fund Approved!", `Your transaction has been approved. check your investment Tab and your email (${email}) for comfirmation.`);
                this.transactionList.remove(this.transactionList.find((c) => c.id === item.id));

                this.withdrawRequests.remove(this.withdrawRequests.find((c) => c.id === item.id));
                this.fundRequests.remove(this.fundRequests.find((c) => c.id === item.id));
                this.fundRequestOption.total -= 1;
                this.dueInvestmentObject.total -= 1;
                this.withdrawRequestOption.total -= 1;
            });
        });

        // On cancel
        this.socket.on(endpoint.cancel_transaction, (item) => {
            runInAction(() => {
                this.transactionList.find((cp) => cp.id === item.id).status = 3;

                this.withdrawRequests.remove(this.withdrawRequests.find((c) => c.id === item.id));
                this.fundRequests.remove(this.fundRequests.find((c) => c.id === item.id));
            });
        });

        // on decline transaction
        this.socket.on(endpoint.decline_transaction, (item) => {
            runInAction(() => {
                this.transactionList.remove(this.transactionList.find((c) => c.id === item.id));
            });
        });

        // on new ROI
        this.socket.on(endpoint.new_ROI, (item) => {
            const { id } = authProvider.getUser();
            // send notification to the targetted user
            if (item.to === id) {
                this.push.generateNotification(
                    "New ROI",
                    `You accout has been credited with $${Intl.NumberFormat("en-UD", {
                        currency: "USD",
                        style: "currency",
                    }).format(item.amount)} ROI.`
                );
                // update currentBalance
                this.investments.find((c) => c.id === item.id).currentBalance += item.amount;
            }
            // remove  from list
            runInAction(() => {
                this.dueInvestmentObject.list.remove(this.dueInvestmentObject.list.find((c) => c.id === item.id));
            });
        });
    }

    // transaction list
    @observable
    transactionList = [];

    @observable
    errorMessage = "";

    @observable
    user = "";
    // new investment
    @observable
    newInvestmentObject = {};

    // update loading state
    @observable
    loading = false;

    // list  of  user's investment
    @observable
    investments = [];

    // list of fund payment channels
    @observable
    fundPaymentTargets = [];

    // transaction fetch option
    @observable
    transactionFetchOption = {
        page: 1,
        pages: 0,
        total: 0,
    };

    limit = 25;

    @observable
    investmentFetchOption = {
        page: 1,
        pages: 0,
        total: 0,
        message: "",
    };

    @observable
    fundRequestOption = {
        page: 1,
        total: 0,
        pages: 0,
    };

    @observable
    withdrawRequestOption = {
        page: 1,
        total: 0,
        pages: 0,
    };
    @observable
    withdrawRequests = [];

    @observable
    fundRequests = [];

    @observable
    dueInvestmentObject = {
        list: [],
        page: 1,
        pages: 0,
        total: 0,
    };

    @observable
    application = {
        data: {},
        setting: {},
    };
    /**
     * Get list of user's transaction
     * @param {string} userid user id
     */
    @action
    getTransactions(userid) {
        runInAction(() => {
            this.user = userid || this.user;
            this.errorMessage = "";
        });
        this.updateLoading(true);
        transactionService
            .getTransactions(this.user, this.transactionFetchOption.page, this.limit)
            .then((cb) => {
                this.updateLoading(false);
                if (cb.status === 200) {
                    runInAction(() => {
                        this.transactionList = cb.docs;
                        this.transactionFetchOption.page = cb.page;
                        this.transactionFetchOption.pages = cb.pages;
                        this.transactionFetchOption.total = cb.total;
                    });
                } else {
                    this.setMessage(cb.message);
                }
            })
            .catch((err) => {
                this.updateLoading(false);
                this.setMessage(err.message);
            });
    }

    @computed
    get isLoading() {
        return this.loading;
    }
    @action
    setMessage = (message) => {
        this.errorMessage = message;
    };

    @action
    updateLoading = (state) => (this.loading = state);

    @action("Get list of user's investments")
    getInvestment = (userid) => {
        investmentService
            .getUserInvestment(userid)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.investments = cb.docs;
                        if (cb.docs.length === 0) this.investmentFetchOption.message = "No investment is added yet! Click on the 'New Investment' button to get started.";
                    } else {
                        runInAction(() => {
                            this.investmentFetchOption.message = cb.message;
                            toast(cb.message, {
                                position: toast.POSITION.TOP_CENTER,
                                type: toast.TYPE.ERROR,
                            });
                        });
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.investmentFetchOption.message = "Failed! Please check your internet connection and try again.";
                });
            });
    };

    // cancel
    @action
    cancelTransaction = (id) => {
        this.updateLoading(true);
        this.setMessage("");
        transactionService
            .cancelTransaction(id)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.socket.emit(endpoint.cancel_transaction, {
                            id: id,
                            target: " cencel",
                        });
                        toast(cb.message, {
                            type: "success",
                        });
                    } else {
                        this.setMessage(cb.message);
                        toast(cb.message, {
                            type: "error",
                        });
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check your internet connection and try again.";
                });
            });
    };

    @action("New user investment")
    newInvestment = async (amount, currencyId, userId, fundTypeId, percent, cardmodel) => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        const res = await investmentService.newInvestment(amount, currencyId, userId, fundTypeId, percent, cardmodel);
        runInAction(() => {
            this.loading = false;
            if (res.status === 200) {
                this.socket.emit(endpoint.new_fund_request, {
                    amount,
                    currency: res.doc.currency,
                    item: res.doc,
                });
                toast(res.message, {
                    type: "success",
                });
            } else {
                this.errorMessage = res.message;
                toast(res.message, {
                    position: toast.POSITION.TOP_CENTER,
                    type: toast.TYPE.ERROR,
                });
            }
        });

        return res;
    };

    @action("New user investment merge")
    Merge = async (userId, percent, fundTarget, ids, service) => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        const res = await investmentService.Merge(userId, percent, fundTarget, ids, service);
        runInAction(() => {
            this.loading = false;
            if (res.status === 200) {
                toast(res.message, {
                    type: "success",
                });
            } else {
                this.errorMessage = res.message;
                toast(res.message, {
                    position: toast.POSITION.TOP_CENTER,
                    type: toast.TYPE.ERROR,
                });
            }
        });

        return res;
    };

    @action
    reviewTransaction = async (id) => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        const cb = await transactionService.reviewFundRequest(id);
        runInAction(() => {
            this.loading = false;
            if (cb.status === 200) {
                if (this.transactionList.length) {
                    const s = this.transactionList.find((c) => c.id === id);
                    if (s) s.status = 1;
                }
            }
        });
        return cb.status === 200;
    };
    @computed
    get hasNew() {
        return this.newInvestmentObject;
    }

    // get  fund request
    @action("Get list of fund request")
    getFundRequest = () => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        transactionService
            .getPendingFundTransactions(this.fundRequestOption.page, this.limit)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.fundRequestOption.page = cb.page;
                        this.fundRequestOption.pages = cb.pages;
                        this.fundRequestOption.total = cb.total;
                        this.fundRequests = cb.docs;
                        if (cb.total === 0) this.errorMessage = "You can refresh the page to check for new record.";
                    } else {
                        this.errorMessage = cb.message;
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    const message = "Failed! Check your internet connection and try again later.";
                    this.errorMessage = message;
                    toast(message, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };

    // approve transaction
    @action
    approveTransaction = (id, user) => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        transactionService
            .approveFundRequest(id, user)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.socket.emit(endpoint.approve_transaction, {
                            id: id,
                            target: "approve",
                            to: user,
                        });
                        toast(cb.message, {
                            type: "success",
                        });
                    } else {
                        this.errorMessage = cb.message;
                        toast(cb.message, {
                            position: toast.POSITION.TOP_CENTER,
                            type: toast.TYPE.ERROR,
                        });
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    const message = "Failed! Check your internet connection and try again later.";
                    this.errorMessage = message;
                    toast(message, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };
    // with approve transaction
    @action
    approveWithdrawalTransaction = (id, user) => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        transactionService
            .approveWithdrawRequest(id, user)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.socket.emit(endpoint.approve_transaction, {
                            id,
                            to: user,
                        });
                        toast(cb.message, {
                            type: "success",
                        });
                    } else {
                        this.errorMessage = cb.message;
                        toast(cb.message, {
                            position: toast.POSITION.TOP_CENTER,
                            type: toast.TYPE.ERROR,
                        });
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    const message = "Failed! Check your internet connection and try again later.";
                    this.errorMessage = message;
                    toast(message, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };

    // send withdraw request
    @action
    sendWithdrawRequest = (id, amount, currency) => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        transactionService
            .withdrawRequest(id, amount, currency)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.socket.emit(endpoint.new_withdraw_request, {
                            id,
                            amount,
                            item: cb.doc,
                        });
                        toast(cb.message, { type: "success" });
                    } else {
                        this.errorMessage = cb.message;
                        toast(cb.message, {
                            position: toast.POSITION.TOP_CENTER,
                            type: toast.TYPE.ERROR,
                        });
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check your internet connection and try again.";
                    toast(this.errorMessage, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };

    // get withdraw request
    getWithdrawalRequest = () => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        transactionService
            .getPendingWithdrawalTransactions(this.withdrawRequestOption.page, this.limit)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.withdrawRequestOption.page = cb.page;
                        this.withdrawRequestOption.pages = cb.pages;
                        this.withdrawRequestOption.total = cb.total;
                        this.withdrawRequests = cb.docs;
                        if (cb.total === 0) this.errorMessage = "You can refresh the page to check for new record.";
                    } else {
                        this.errorMessage = cb.message;
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check  your internet connection and try again later.";
                    toast(this.errorMessage, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };
    @action
    getDueInvestment = () => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        investmentService
            .getInvestmentDueForIncrease(this.dueInvestmentObject.page, this.limit)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        this.dueInvestmentObject.page = cb.page;
                        this.dueInvestmentObject.pages = cb.pages;
                        this.dueInvestmentObject.total = cb.total;
                        this.dueInvestmentObject.list = cb.docs;
                        if (cb.total === 0) this.errorMessage = "No Investment is due increasement.";
                    } else {
                        this.errorMessage = cb.message;
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check  your internet connection and try again later.";
                    toast(this.errorMessage, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };
    @action
    loadMoreDueInvestment = (page) => {
        runInAction(() => {
            this.dueInvestmentObject.page = page;
        });
        this.getDueInvestment();
    };
    @action("credit user investment (ROI)")
    increaseInvestment = (id, amount) => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        investmentService
            .IncreaseInvestment(id, amount)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        const { user } = this.dueInvestmentObject.list[this.dueInvestmentObject.list.findIndex((x) => x.id === id)];
                        toast(cb.message, { type: "success" });
                        this.socket.emit(endpoint.new_ROI, {
                            id,
                            amount,
                            to: user.id,
                        });
                    } else {
                        this.errorMessage = cb.message;
                        toast(this.errorMessage, {
                            position: toast.POSITION.TOP_CENTER,
                            type: toast.TYPE.ERROR,
                        });
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check your internet connection and try again.";
                    toast(this.errorMessage, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };
    @computed
    get getTotalROI() {
        const nums = this.investments.map((c) => c.currentBalance);
        return nums.reduce((a, b) => a + b, 0);
    }

    @computed
    get getSumInvestment() {
        const nums = this.investments.map((c) => c.investmentMade);
        return nums.reduce((a, b) => a + b, 0);
    }

    @action("increase user investment")
    topupInvestmentMade = (amount, currencyId, investmentId) => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        transactionService
            .newTransaction(amount, currencyId, investmentId)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        toast(cb.message, { type: "success" });
                    } else {
                        toast(cb.message, {
                            position: toast.POSITION.TOP_CENTER,
                            type: toast.TYPE.ERROR,
                        });
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check your internet connection and try again.";
                    toast(this.errorMessage, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };

    @action("close user investment")
    closeUserSingleInvestment = (investmentId) => {
        runInAction(() => {
            this.loading = true;
            this.errorMessage = "";
        });
        investmentService
            .closeUserInvestment(investmentId)
            .then((cb) => {
                runInAction(() => {
                    this.loading = false;
                    if (cb.status === 200) {
                        toast(cb.message, { type: "success" });
                        this.investments.remove(this.investments.findIndex((c) => c.id === investmentId));
                    } else {
                        toast(cb.message, {
                            position: toast.POSITION.TOP_CENTER,
                            type: toast.TYPE.ERROR,
                        });
                    }
                });
            })
            .catch(() => {
                runInAction(() => {
                    this.loading = false;
                    this.errorMessage = "Failed! Please check your internet connection and try again.";
                    toast(this.errorMessage, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                });
            });
    };
    // load more fund request
    @action
    loadMoreFundRequest = (page) => {
        runInAction(() => {
            this.fundRequestOption.page = page;
        });
        this.getFundRequest();
    };

    @action
    loadMoreWithdrawalRequest = (page) => {
        runInAction(() => {
            this.withdrawRequestOption.page = page;
        });
        this.getWithdrawalRequest();
    };
    @action
    loadMoreTransaction = (page) => {
        runInAction(() => {
            this.transactionFetchOption.page = page;
        });
        this.getTransactions("");
    };

    @action
    getFundPaymentChannel = () => {
        this.updateLoading(true);
        investmentService
            .GetFundPaymentTarget()
            .then((cb) => {
                this.updateLoading(false);
                runInAction(() => {
                    this.fundPaymentTargets = cb.docs;
                });
            })
            .catch((err) => {
                this.updateLoading(false);
                toast(err.message, {
                    position: toast.POSITION.TOP_CENTER,
                    type: toast.TYPE.ERROR,
                });
            });
    };

    @action
    GetApplicationSettingsData = async () => {
        this.updateLoading(true);
        const cb = await transactionService.GetApplicationSetting();
        this.updateLoading(false);
        if (cb.status === 200) {
            return cb.doc;
        } else {
            toast(cb.message, {
                position: toast.POSITION.TOP_CENTER,
                type: toast.TYPE.ERROR,
            });
            throw new Error(cb.message);
        }
    };

    @action
    UpdateAppData = (trans, consultant, investor, investment) => {
        this.updateLoading(true);
        transactionService
            .UpdateApplicationData(trans, investment, investor, consultant)
            .then((cb) => {
                this.updateLoading(false);
                if (cb.status === 200) {
                    toast(cb.message, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.SUCCESS,
                    });
                } else {
                    toast(cb.message, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                }
            })
            .catch((err) => {
                this.updateLoading(false);
                toast(err.message, {
                    position: toast.POSITION.TOP_CENTER,
                    type: toast.TYPE.ERROR,
                });
            });
    };
    @action
    UpdateAppSetting = (name, vEmail, vWallet, nFund, nFundReminder, percent) => {
        this.updateLoading(true);
        transactionService
            .UpdateApplicationSetting(name, vEmail, vWallet, nFund, nFundReminder, percent)
            .then((cb) => {
                this.updateLoading(false);
                if (cb.status === 200) {
                    toast(cb.message, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.SUCCESS,
                    });
                } else {
                    toast(cb.message, {
                        position: toast.POSITION.TOP_CENTER,
                        type: toast.TYPE.ERROR,
                    });
                }
            })
            .catch((err) => {
                this.updateLoading(false);
                toast(err.message, {
                    position: toast.POSITION.TOP_CENTER,
                    type: toast.TYPE.ERROR,
                });
            });
    };
}

export const transactionStore = new TransactionStore();
